* {
  margin: 0;
  padding: 0;
}
.header {
  font-size: 50px;
  color: white;
  background-color: #343a40;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  background: #023e8a;
}
.keyboard-container {
  padding-left: 250px;
  background-color: #023e8a;
  margin: 250px 0 0 0;
  width: 100%;
  height: 300px;
}

.accidentals-container {
  width: 960px;
  display: grid;
  grid-template-columns: repeat(42, 1fr);
  height: 66%;
  justify-items: center;
}

.accidentals-container button {
  width: 58px;
  background: #1b1b1e;
  border: 1px solid #fff;
  border-radius: 0 0 4px 4px;
  cursor: pointer;
  top: -299px;
  position: relative;
  z-index: 1;
  border-top: none;
}

.accidentals-container button.C {
  grid-column: 2 / span 4;
}

.accidentals-container button.D {
  grid-column: / span 2;
}

.accidentals-container button.F {
  grid-column: 10 / span 3;
}

.accidentals-container button.G {
  grid-column: 14 / span 2;
}

.accidentals-container button.A {
  grid-column: 17 / span 2;
}
.accidentals-container button.C1 {
  grid-column: 25 / span 2;
}
.accidentals-container button.D1 {
  grid-column: 28 / span 2;
}
.accidentals-container button.F1 {
  grid-column: 34 / span 2;
}
.accidentals-container button.G1 {
  grid-column: 37 / span 2;
}
.accidentals-container button.A1 {
  grid-column: 40 / span 1;
}

.naturals-container {
  width: 960px;
  display: grid;
  grid-template-columns: repeat(14, 1fr);
  height: 300px;
}

.naturals-container button {
  background: #fff;
  border: 1px solid #1b1b1e;
  border-radius: 0 0 6px 6px;
}

button {
  cursor: pointer;
}

button:active,
button:focus {
  border: 3px solid #4fb7ec;
  outline: rgba(225, 225, 225, 0);
}

.naturals-container p {
  font-size: 1.75em;
  top: 100px;
  position: relative;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.9), 0 -1px 1px rgba(0, 0, 0, 0.1);
  color: #1b1b1e;
}
